.header {
  display: flex;
  justify-content: space-between;
  max-width: 880px;
  padding-top: 45px;
  padding-bottom: 36px;
  margin-left: auto;
  margin-right: auto;
  border-bottom: 1px solid rgb(84,84,84,0.7);
}
@media screen and (max-width: 550px) {
  .header {
    padding-top: 28px;
    padding-bottom: 27px;
  }
}

